import React, { Component } from 'react';

import { connect } from 'react-redux';
import { Switch, Route } from 'react-router-dom';
import DecisionList from './DecisionList';
import DecisionDetail from './DecisionDetail';

// export default function ScrumBoard({ match }) {
//   return (
//     <>
//       <Switch>
//         <Route exact path={`${match.path}`} component={DecisionList}/>
//         <Route path={`${match.path}/project/:id`} component={DecisionDetail} />
//       </Switch>
//     </>
//   );
// }

class ScrumBoard extends Component {
  render() {
    return (
      <>
        <Switch>
          <Route
            exact
            path={`${this.props.match.path}`}
            component={DecisionList}
          />
          <Route
            path={`${this.props.match.path}/project/:id`}
            component={DecisionDetail}
          />
        </Switch>
      </>
    );
  }
}

export default connect(state => {
  return state.Claim;
}, {})(ScrumBoard);
