import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Row, Col, Table, Typography, Button } from 'antd';
import Information from '../../Information';
import moment from 'moment';
import FileDownloader from '../../../helpers/fileDownloader';
class BankDocs extends Component {
  render() {
    var dataClaimForm = [];
    var dataDocs = [];
    this.props.claim.ClaimInfoVisits.forEach(visit => {
      visit.ClaimInfoVisitsFiles.forEach(claimInfoVisitsFiles => {
        claimInfoVisitsFiles.visitDate = visit.dateOfAdmissionVisit;
        dataClaimForm.push(claimInfoVisitsFiles);
      });
      visit.BillingInfoFiles.forEach(billingInfoFiles => {
        billingInfoFiles.visitDate = visit.dateOfAdmissionVisit;
        dataClaimForm.push(billingInfoFiles);
      });
      visit.DocumentsFiles.forEach(documentsFiles => {
        documentsFiles.visitDate = visit.dateOfAdmissionVisit;
        dataDocs.push(documentsFiles);
      });
    });

    const columns = [
      {
        title: 'Name',
        dataIndex: 'name',
        key: 'name',
        render: (text, record) => {
          return (
            <Button
              type="link"
              onClick={e => {
                FileDownloader(record.url, record.name);
              }}
            >
              {text}
            </Button>
          );
        },
      },
      {
        title: 'Visit Date',
        dataIndex: 'visitDate',
        key: 'visitDate',
        render: (text, record) => {
          return moment(text).format('DD MMM YYYY');
        },
      },
      {
        title: 'Uploaded',
        dataIndex: 'createdAt',
        key: 'createdAt',
        render: (text, record) => {
          return moment(text).format('DD MMM YYYY');
        },
      },
    ];

    const columnsDocs = [
      {
        title: 'Name',
        dataIndex: 'name',
        key: 'name',
        render: (text, record) => {
          return (
            <Button
              type="link"
              onClick={e => {
                FileDownloader(record.url, record.name);
              }}
            >
              {text}
            </Button>
          );
        },
      },
      {
        title: 'Visit Date',
        dataIndex: 'visitDate',
        key: 'visitDate',
        render: (text, record) => {
          return moment(text).format('DD MMM YYYY');
        },
      },
      {
        title: 'Uploaded',
        dataIndex: 'createdAt',
        key: 'createdAt',
        render: (text, record) => {
          return moment(text).format('DD MMM YYYY');
        },
      },
      {
        title: 'Notes',
        dataIndex: 'notes',
        key: 'notes',
      },
    ];

    const rowBlockstyling = {
      marginTop: '5%',
    };

    return (
      <>
        <Row type="flex" align="middle" offset={1}>
          <Col md={4} sm={11} xs={11}>
            <p
              style={{
                fontFamily: 'SegoeUI, Segoe UI',
                fontWeight: 'bold',
                fontSize: 14,
                color: '#f17d27',
                marginBottom: '0',
              }}
            >
              {'UCN:'}{' '}
            </p>
            <p
              style={{
                fontFamily: 'SegoeUI, Segoe UI',
                fontSize: 14,
                color: 'white',
                marginBottom: '0',
              }}
            >
              {this.props.claim.id}
            </p>
          </Col>
          <Col md={4} sm={11} xs={11}>
            <p
              style={{
                fontFamily: 'SegoeUI, Segoe UI',
                fontSize: 14,
                color: 'white',
                marginBottom: '0',
              }}
            >
              {'Patient Name: '}
            </p>
            <p
              style={{
                fontFamily: 'SegoeUI, Segoe UI',
                fontSize: 14,
                color: 'white',
                marginBottom: '0',
              }}
            >
              {this.props.claim.patientFirstName ||
              this.props.claim.patientFirstName
                ? this.props.claim.patientFirstName +
                  ' ' +
                  this.props.claim.patientLastName
                : ''}
            </p>
          </Col>
        </Row>
        <Row style={rowBlockstyling}>
          <Col span={4}>
            <Typography.Title level={4}>Bank Details:</Typography.Title>
            <Row style={rowBlockstyling}>
              <Information
                title="Account Holders Name"
                content={this.props.claim.accountHoldersName}
              />
            </Row>
            <Row style={rowBlockstyling}>
              <Information
                title="Bank Name"
                content={this.props.claim.bankName}
              />
            </Row>
            <Row style={rowBlockstyling}>
              <Information
                title="Bank Address"
                content={this.props.claim.bankAddress}
              />
            </Row>
            <Row style={rowBlockstyling}>
              <Information
                title="Reimbursement Currency"
                content={this.props.claim.reimbusementCurrency}
              />
            </Row>
            <Row style={rowBlockstyling}>
              <Information
                title="Bank Account Number"
                content={this.props.claim.bankAccountNumber}
              />
            </Row>
            <Row style={rowBlockstyling}>
              <Information
                title="IBAN Code / Sort Code"
                content={this.props.claim.ibanCodeSortCode}
              />
            </Row>
            <Row style={rowBlockstyling}>
              <Information
                title="SWIFT Code"
                content={this.props.claim.swift}
              />
            </Row>
          </Col>
          <Col span={20}>
            <Table rowKey={'id'} dataSource={dataClaimForm} columns={columns} />
            ;
            <Table rowKey={'id'} dataSource={dataDocs} columns={columnsDocs} />;
          </Col>
        </Row>
      </>
    );
  }
}

export default connect(state => ({}), {})(BankDocs);
