import React from 'react';
import { Table } from 'antd';
import TableStyleWrapper from './tableSub.style';
import {
  EditableFormRow,
  EditableCell,
} from '@isomorphic/shared/isomorphic/components/Tables/CustomizedCellsNumber';
import _ from 'lodash';
import { sum } from 'd3-array';
import { constant } from '../../../settings';

export default function TableSub(props) {
  // Declare a new state variable, which we'll call "count"
  var billingData = _.chain(props.visit.BillingInfos)
    .groupBy('BenefitCategory.id')
    .map((v, i) => {
      var newArray = v.map(billing => {
        //ClaimedAmount
        let ptClaimedAmount = props.patientClaim.reduce((acc, curr) => {
          return (
            acc +
            curr.ClaimInfoVisits.reduce((visitAcc, visitCurr) => {
              return (
                visitAcc +
                visitCurr.BillingInfos.reduce((billingAcc, billingCurr) => {
                  //console.log('billingCurr.id', billingCurr.id)
                  return billingAcc + billingCurr.billingSubCat ===
                    billing.BenefitSubCategory.id
                    ? billingCurr.value
                    : 0;
                }, 0)
              );
            }, 0)
          );
        }, 0);

        let times;
        if (billing.billingSubCat === 25 || billing.billingSubCat === 27)
          times = props.patientClaim.reduce((acc, curr) => {
            return (
              acc +
              curr.ClaimInfoVisits.reduce((visitAcc, visitCurr) => {
                return (
                  visitAcc +
                  visitCurr.BillingInfos.reduce((billingAcc, billingCurr) => {
                    //console.log('billingCurr.id', billingCurr.id)
                    return billingAcc +
                      (billingCurr.billingSubCat === billing.billingSubCat &&
                        billingCurr.approved > 0)
                      ? 1
                      : 0;
                  }, 0)
                );
              }, 0)
            );
          }, 0);

        //condition
        let condition = billing.BenefitSubCategory[props.claim.policyType];
        //Remaining Entitlement
        let remainEntitlement = isNaN(condition)
          ? 'N/A'
          : (condition - ptClaimedAmount).toFixed(constant.fixedDigital);

        if (billing.billingSubCat === 39)
          billing.BenefitSubCategory.name = props.visit.BillingInfoOtherNames[0]
            ? props.visit.BillingInfoOtherNames[0].value
            : '';

        return {
          id: billing.id,
          name: billing.BenefitSubCategory.name,
          condition: condition,
          value: Number(billing.value).toFixed(constant.fixedDigital),
          approved: Number(billing.approved).toFixed(constant.fixedDigital),
          claimed:
            ptClaimedAmount.toFixed(constant.fixedDigital) +
            (times !== undefined ? ' (' + times + ' visits)' : ''),
          remainEntitlement: remainEntitlement,
        };
      });
      return {
        id: 'cat' + v[0].BenefitCategory.id,
        name: v[0].BenefitCategory.name,
        value: sum(newArray, child => child.value),
        approved: sum(newArray, child => child.approved),
        children: newArray,
      };
    })
    .value();
  const components = {
    body: {
      row: EditableFormRow,
      cell: EditableCell,
    },
  };
  const columns = [
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'id',
    },
    {
      title: 'Condition',
      dataIndex: 'condition',
    },
    {
      title: 'Approved YTD',
      dataIndex: 'claimed',
    },
    {
      title: 'Remaining Entitlement',
      dataIndex: 'remainEntitlement',
    },
    {
      title: 'Amounts Claimed in USD',
      render: (text, record) => {
        if (record.hasOwnProperty('children')) return false;
        return record.value;
      },
    },
    {
      title: 'Amounts Claimed in B/C',
      render: (text, record) => {
        if (record.hasOwnProperty('children'))
          return props.visit.billingCurrency;
        return (record.value * props.visit.billingRate).toFixed(
          constant.fixedDigital
        );
      },
    },
    {
      title: 'Amounts Approved in B/C',
      render: (text, record) => {
        return (record.approved * props.visit.billingRate).toFixed(
          constant.fixedDigital
        );
      },
    },
    {
      title: 'Amounts Approved in USD',
      render: (text, record) => {
        if (record.hasOwnProperty('children')) return false;
        return record.approved;
      },
    },
    {
      title: 'Amounts Approved in R/C',
      render: (text, record) => {
        if (record.hasOwnProperty('children'))
          return props.claim.reimbusementCurrency;
        return (record.approved * props.claim.RCExchangeRate).toFixed(
          constant.fixedDigital
        );
      },
    },
  ];
  return (
    <TableStyleWrapper>
      <Table
        columns={columns}
        components={components}
        rowKey={'id'}
        dataSource={billingData}
      />
    </TableStyleWrapper>
  );
}
